.homepage-component {
  min-height: 100vh;
  background: url("homepage-assets/home-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 90% 50%;
  margin-top: -70px;
  background-attachment: fixed; }
  .homepage-component .bg-holder {
    min-height: 100vh;
    padding-top: 255px;
    padding-bottom: 50px;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(50, 50, 50, 0.8)), to(rgba(50, 50, 50, 0.5)));
    background: -webkit-linear-gradient(top, rgba(50, 50, 50, 0.8) 0%, rgba(50, 50, 50, 0.5) 100%);
    background: -o-linear-gradient(top, rgba(50, 50, 50, 0.8) 0%, rgba(50, 50, 50, 0.5) 100%);
    background: linear-gradient(to bottom, rgba(50, 50, 50, 0.8) 0%, rgba(50, 50, 50, 0.5) 100%); }
  .homepage-component h1 {
    font-weight: 300;
    color: #fff;
    text-align: center;
    margin: 0 5px 142px;
    margin-bottom: 30px;
    line-height: 1.3; }
  .homepage-component h4 {
    font-size: 24px;
    font-weight: 900;
    color: #7e6e7a;
    line-height: 1.3;
    margin-bottom: 10px; }
  .homepage-component p {
    font-family: 'semplicitapro', sans-serif;
    font-weight: 500;
    color: #7e6e7a;
    font-size: 16px;
    opacity: .7;
    line-height: 1.3;
    margin-bottom: 10px; }
  .homepage-component .no-wrap {
    font-family: 'semplicitapro', 'sans-serif';
    font-size: 46px;
    font-weight: 300;
    white-space: nowrap; }
  .homepage-component .homepage-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 1076px;
    margin: auto; }
  .homepage-component .info-box {
    background-color: #e9e9e9;
    border-left: 60px solid #f4f4f4;
    margin: 20px;
    padding: 24px 45px;
    position: relative;
    width: 498px;
    height: 437px; }
  .homepage-component .homepage-icon {
    width: 60px;
    position: absolute;
    top: 185px;
    left: -30px; }
  .homepage-component .homepage-logo {
    width: 50px;
    position: relative;
    top: 6px;
    left: 1px; }
  .homepage-component .header-button-top {
    margin-top: 30px;
    margin-bottom: 200px; }
  .homepage-component button {
    background-color: #df4054;
    padding: 16px 23px 15px;
    border: 0;
    display: block;
    margin: 20px auto 0;
    cursor: pointer;
    -webkit-transition: background-color .15s ease-in-out;
    -o-transition: background-color .15s ease-in-out;
    transition: background-color .15s ease-in-out; }
    .homepage-component button a {
      color: #fff; }
  .homepage-component button:hover {
    background-color: #c12035; }

@media screen and (max-width: 1075px) {
  .homepage-component .homepage-container {
    width: 538px; } }

@media screen and (max-width: 680px) {
  .homepage-component h1, .homepage-component .no-wrap {
    font-size: 30px; }
  .homepage-component .homepage-logo {
    width: 36px; } }

@media screen and (max-width: 537px) {
  .homepage-component .homepage-container {
    width: 100%; }
  .homepage-component .info-box {
    background-color: #e9e9e9;
    border-left: 0;
    border-top: 60px solid #f4f4f4;
    margin: 20px 0;
    width: 100%;
    height: unset; }
  .homepage-component .homepage-icon {
    width: 60px;
    position: static;
    display: block;
    margin: -54px auto 0; } }



