/********  APP STYLES  ********/
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "alda";
  font-weight: 500;
  font-size: 16px;
  outline: none; }

body {
  min-height: 100vh;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9c6c7), to(#e9e9e9));
  background: -webkit-linear-gradient(top, #c9c6c7 0%, #e9e9e9 100%);
  background: -o-linear-gradient(top, #c9c6c7 0%, #e9e9e9 100%);
  background: linear-gradient(to bottom, #c9c6c7 0%, #e9e9e9 100%); }

a {
  text-decoration: none;
  color: #444;
  font-family: "semplicitapro", sans-serif; }

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
  font-family: "semplicitapro", sans-serif;
  font-weight: 300;
  color: #006699; }

h2 {
  font-weight: 900; }

p {
  color: #37414d;
  padding: 0;
  margin: 0;
  font-size: 16px; }



