.header-component {
  background-color: #f4f4f4;
  padding: 21px 0;
  position: relative;
  margin-bottom: 70px; }
  .header-component h1 {
    margin: 0 0 7px;
    padding: 0;
    text-align: center;
    color: #df4054;
    font-weight: 900; }
  .header-component .logout {
    position: absolute;
    font-size: 20px;
    top: 12px;
    right: 15px;
    font-weight: 900;
    color: #df4054;
    -webkit-transition: opacity .15s ease-in-out;
    -o-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out; }
  .header-component .login {
    font-size: 20px;
    font-weight: 900;
    position: absolute;
    right: 12px;
    top: 12px;
    color: #df4054;
    -webkit-transition: opacity .15s ease-in-out;
    -o-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out; }
  .header-component .login:hover, .header-component .logout:hover {
    color: #c12035; }
  .header-component .user, .header-component .group {
    color: #7e6e7a;
    position: absolute;
    -webkit-transition: opacity .15s ease-in-out;
    -o-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out;
    opacity: .8;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center; }
    .header-component .user img, .header-component .group img {
      width: 20px;
      height: 20px;
      margin-left: 6px; }
  .header-component .user {
    right: 15px;
    top: 47px; }
  .header-component .group {
    right: 15px;
    top: 73px; }
  .header-component .header-logo {
    width: 75px;
    margin: 0 10px -17px 15px; }
  .header-component .user:hover, .header-component .group:hover {
    opacity: 1; }
  .header-component .nav-tabs {
    position: absolute;
    bottom: -40px;
    width: 100%;
    display: -ms-flexbox;
    display: flex; }
    .header-component .nav-tabs a {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      background-color: rgba(244, 244, 244, 0.6);
      color: #bfbcbd;
      -webkit-box-sizing: initial;
              box-sizing: initial;
      font-size: 20px;
      padding: 10px 0px;
      margin-right: 7px;
      width: 130px;
      position: relative;
      top: 2px;
      font-weight: 900;
      opacity: .7;
      -webkit-transition: all .15s ease-in-out;
      -o-transition: all .15s ease-in-out;
      transition: all .15s ease-in-out; }
      .header-component .nav-tabs a.active {
        background-color: #f4f4f4;
        color: #7e6e7a;
        opacity: 1; }
      .header-component .nav-tabs a:hover {
        background-color: #c6;
        color: #c3;
        opacity: 1; }

@media screen and (max-width: 700px) {
  .header-component h1 {
    margin-bottom: 15px; }
  .header-component .login, .header-component .user, .header-component .group, .header-component .logout {
    display: block;
    position: static;
    text-align: center;
    margin: 7px; }
    .header-component .login img, .header-component .user img, .header-component .group img, .header-component .logout img {
      position: relative;
      top: 3px; }
  .header-component .logout {
    margin-top: 20px; } }

@media screen and (max-width: 550px) {
  .header-component {
    margin-bottom: 39px; }
    .header-component .nav-tabs {
      -ms-flex-direction: column;
          flex-direction: column;
      position: unset;
      /*background-color: #cccbcb;*/
      padding: 10px 0 15px;
      margin-bottom: -34px;
      margin-top: -9px; }
      .header-component .nav-tabs a {
        width: 100%;
        /*background-color: $c3;*/ }
        .header-component .nav-tabs a.active {
          color: #fff;
          background-color: #c9c6c7; } }

@media screen and (max-width: 400px) {
  .header-component h1 {
    font-size: 35px; }
  .header-component .header-logo {
    width: 64px; }
  .header-component .nav-tabs a {
    margin-right: 0; } }



