.switch-wrapper {
  -webkit-transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1);
  -o-transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1);
  transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1);
  width: 100%; }

.switch-wrapper.fade-enter {
  opacity: 0.5;
  position: absolute;
  -webkit-transform: translate(-100%);
      -ms-transform: translate(-100%);
          transform: translate(-100%);
  top: 105px; }

.switch-wrapper.fade-exit {
  opacity: 0.5;
  position: absolute;
  top: 179px;
  -webkit-transform: translate(100%);
      -ms-transform: translate(100%);
          transform: translate(100%); }

@media screen and (max-width: 700px) {
  .switch-wrapper.fade-exit {
    top: 290px; } }

@media screen and (max-width: 550px) {
  .switch-wrapper.fade-exit {
    top: 456px; } }

@media screen and (max-width: 400px) {
  .switch-wrapper.fade-exit {
    top: 445px; } }



