@font-face {
  font-family: 'Journal-Regular';
  src: url("./assets/Journal-Regular.otf"); }

.comment-card-component {
  background-color: #f4f4f4;
  width: 360px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  margin-bottom: 30px;
  position: relative;
  padding-bottom: 37px; }
  .comment-card-component:nth-of-type(odd) {
    margin-right: 30px; }
  .comment-card-component .card-header {
    background-color: #c9c6c7;
    padding: 14px 0 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around; }
  .comment-card-component .card-footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
    position: absolute;
    bottom: 10px;
    right: 13px; }
  .comment-card-component h3, .comment-card-component h4 {
    font-weight: 900; }
  .comment-card-component h6 {
    font-weight: 900;
    color: #7e6e7a; }
  .comment-card-component h5 {
    color: #fff;
    font-size: 16px;
    font-weight: 900; }
  .comment-card-component p {
    font-family: 'Journal-Regular';
    font-size: 30px;
    padding: 16px 25px;
    color: #554559; }
  .comment-card-component img {
    width: 30px;
    margin-right: 5px; }

@media screen and (max-width: 750px) {
  .comment-card-component:nth-of-type(odd) {
    margin-right: 0; } }

@media screen and (max-width: 375px) {
  .comment-card-component {
    width: 100%; } }



