.user-component {
  padding: 3px 3px 50px;
  width: 100%;
  -webkit-transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1);
  -o-transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1);
  transition: 0.25s cubic-bezier(0.78, 0.07, 0.35, 1); }
  .user-component .join-group {
    width: 750px;
    margin: auto;
    display: block;
    padding: 0 25px;
    color: #df4054;
    -webkit-transition: color .15s ease-in-out;
    -o-transition: color .15s ease-in-out;
    transition: color .15s ease-in-out;
    font-weight: 900;
    text-align: right;
    font-size: 20px; }
  .user-component .join-group:hover {
    color: #c12035; }

.user-component.fade-enter {
  opacity: 0.5;
  position: absolute;
  -webkit-transform: translate(-100%);
      -ms-transform: translate(-100%);
          transform: translate(-100%);
  top: 105px; }

.user-component.fade-exit {
  opacity: 0.5;
  position: absolute;
  top: 179px;
  -webkit-transform: translate(100%);
      -ms-transform: translate(100%);
          transform: translate(100%); }

@media screen and (max-width: 750px) {
  .user-component {
    padding: 0 0 50px; }
    .user-component .join-group {
      width: 100%; } }



